import React, { useState } from 'react';
import { useActions } from '../../hooks/useActions';

import { addRecipe } from '../../store/action-creators/recipe';
import { RecipeTypes } from '../../types/recipe';

const moment = require('moment');


const RecipeForm: React.FC = () => {

    const date = moment(Date.now()).format('LL').toString();

    const [newRecipe, setNewRecipe] = useState({} as RecipeTypes);

    const { addRecipe } = useActions();


    const handleChangeData = (e: React.ChangeEvent<HTMLInputElement>
        | React.ChangeEvent<HTMLSelectElement>
        | React.ChangeEvent<HTMLTextAreaElement>) => {
        const name = e.target.name;
        const info = e.target.value;
        setNewRecipe({ ...newRecipe, [name]: info });
    }

    const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault();
        newRecipe.date = new Date;
        addRecipe(newRecipe);
        setNewRecipe({ author: '', title: '', time: '', calories: '', ingredients: '', steps: '', meal: '', img: '' } as RecipeTypes);
    }


    return (
        <form className='recipeForm' onSubmit={handleSubmit}>
            <input className='recipeForm__input' name='author' placeholder=' ' type="text" required onChange={handleChangeData} value={newRecipe.author} />
            <input className='recipeForm__input' name='title' placeholder=' ' type="text" required onChange={handleChangeData} value={newRecipe.title} />
            <input className='recipeForm__input' name='time' placeholder=' , ' type="number" required onChange={handleChangeData} value={newRecipe.time} />
            <input className='recipeForm__input' name='calories' placeholder=' , ' type="number" required onChange={handleChangeData} value={newRecipe.calories} />
            <textarea className='recipeForm__input recipeForm__textarea' name='ingredients' placeholder='' required onChange={handleChangeData} value={newRecipe.ingredients} />
            <textarea className='recipeForm__input recipeForm__textarea' name='steps' placeholder='  ' required onChange={handleChangeData} value={newRecipe.steps} />
            <select className='recipeForm__input recipeForm__select' name='meal' onChange={handleChangeData} value={newRecipe.meal}>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
            </select>
            <input className='recipeForm__input' name='img' placeholder='   ' type="url" required onChange={handleChangeData} value={newRecipe.img} />
            <input className='recipeForm__input recipeForm__date' name='date' type="text" value={date} />
            <button type="submit" className="recipeForm__button"> </button>
        </form>
    );
};

export default RecipeForm;